import axios from 'axios';
let tokenjwt = localStorage.getItem('token');

export default class LitService {

    getlit(residenceId) {
     
        let respo;
        try {
          const response =  axios.get(`${process.env.VUE_APP_HOST_BACKEND}/lit/view/${residenceId}`, {
            headers: {
              Authorization: `${tokenjwt}`,
            },
          });
          respo=response;
          

        } catch (error) {
          console.error(error);
        }
        return respo;

  }
  addlit(data){
    let resp = {
      type: null,
      msg: null,
      summary: null
    }
    let body = {
      prixch:data.Prix,
      cotionch:data.Cotion,
    }
    const request = axios.post(`${process.env.VUE_APP_HOST_BACKEND}/lit/add/${data.Residenceid}/${data.blocid}/${data.etageid}/${data.appartmentid}/${data.chambreid}`,body, {
      headers: {
        Authorization: `${tokenjwt}`,
      },
    })
    .then(response => {
      if (response.data.success){
        resp.type = "success";
        resp.msg = "Lit ajoutée avec succès";
        resp.summary = "Action Terminée";

      }
      else{
        resp.type = "error";
        resp.msg = response.data.msg;
        resp.summary = "ERREUR";
      }
      return resp;
    })
    .catch(error => {
      alert('Wrong user account ' +error )
    });
    return request;

  }
  updateLit(data){
    let resp = {
      type: null,
      msg: null,
      summary: null
    }
    let body={
      Prix:data.prixlit,
      Cotion:data.cautionlit
    }
    const request = axios.put(`${process.env.VUE_APP_HOST_BACKEND}/lit/edit/${data.Residenceid}/${data.blocid}/${data.etageid}/${data.appartmentid}/${data.indexch}/${data.indexlit}`,body, {
      headers: {
        Authorization: `${tokenjwt}`,
      },
    })
    .then(response => {
      if (response.data.success){
        resp.type = "success";
        resp.msg = "Lit modifie avec succès";
        resp.summary = "Action Terminée";

      }
      else{
        resp.type = "error";
        resp.msg = response.data.msg;
        resp.summary = "ERREUR";
      }
      return resp;
    })
    .catch(error => {
      alert('Wrong user account ' +error )
    });
    return request;

  }
  deleteLit(data){
    let resp = {
      type: null,
      msg: null,
      summary: null
    }

    const request = axios.delete(`${process.env.VUE_APP_HOST_BACKEND}/lit/delete/${data.Residenceid}/${data.blocid}/${data.etageid}/${data.appartmentid}/${data.indexch}/${data.indexlit}`, {
      headers: {
        Authorization: `${tokenjwt}`,
      },
    })
    .then(response => {
      if (response.data.success){
        resp.type = "success";
        resp.msg = "Lit modifie avec succès";
        resp.summary = "Action Terminée";

      }
      else{
        resp.type = "error";
        resp.msg = response.data.msg;
        resp.summary = "ERREUR";
      }
      return resp;
    })
    .catch(error => {
      alert('Wrong user account ' +error )
    });
    return request;

  }
 
}
