<script setup>
import { ref, onMounted,watchEffect } from 'vue';
import LitService from '../../service/servicebiens/LitService';
import { useToast } from 'primevue/usetoast';

import { useRoute } from 'vue-router';
const litservices = new LitService();
const staticObject  = ref(null);
const LitData = ref(null);
const sliderValue = ref(0);
const LitDataEdit = ref(null);

const route = useRoute();  
const toast = useToast();
const Residenceid = route.params.id;
const dt = ref(null);

const litaddDialog = ref(false);
const liteditDialog=ref(false);
const submitted = ref(false);


let EtagedropdownValues = ref([]);
let EtagedropdownValue = ref([]);

let blocdropdownValues = ref(null);
let blocdropdownValue = ref(null);

let appartmentdropdownValues = ref([]);
let appartmentdropdownValue = ref([]);

let chambredropdownValues = ref([]);
let chambredropdownValue = ref([]);

let deletedata=ref({})
let deletedialog=ref(false)

const refreshComponent = ()=>{
    readResidence(Residenceid);

}
const readResidence = async (id) => {
    const response = await litservices.getlit(id);
    staticObject.value = response.data.staticObject;
    LitData.value=response.data.appartements;
    blocdropdownValues.value=staticObject.value.map(bloc => ({
        id: bloc.blocid,    
        name: bloc.blocname,
    }));

    
};
const getetagesbloc = (data) => {
    EtagedropdownValues.value = [];
    const aboutetages=staticObject.value.find(bloc => bloc.blocid === data.id);
    aboutetages.etagedata.map(etage => {
        EtagedropdownValues.value.push({
            id: etage.etageid,    
            name: `ETAGE ${etage.etagenumber}`,
        })
    })
    appartmentdropdownValue.value = null;
    chambredropdownValue.value = null;
}
const getappartmentsetage = (data) => {
    appartmentdropdownValues.value = [];
    const aboutappartments=staticObject.value.find(bloc => bloc.blocid === blocdropdownValue.value.id);
    const aboutetages = aboutappartments.etagedata.find(etage => etage.etageid === data.id);
    aboutetages.appartementdata.map(appart => {
        appartmentdropdownValues.value.push({
            id: appart.appartmentId,    
            name: `APPARTEMENT ${appart.appartmentnumber}`,
        })

    })
    chambredropdownValue.value = null;
};
const getchambappart =(data)=>{
    const aboutappartments=staticObject.value.find(bloc => bloc.blocid === blocdropdownValue.value.id);
    const aboutetages = aboutappartments.etagedata.find(etage => etage.etageid === EtagedropdownValue.value.id);
    const aboutchambres = aboutetages.appartementdata.find(appart => appart.appartmentId === data.id);
    chambredropdownValues.value = [];
    for (let index = 1; index <= aboutchambres.nbchabre ;  index++) {
        chambredropdownValues.value.push({
            id: index,    
            name: `CHAMBRE ${index}`,
        })
        
    }
    
}
const exportCSV = () => {
    dt.value.exportCSV();
};
const litaddData =ref({})
const openNew = () => {
    litaddDialog.value = true;
    EtagedropdownValue.value = null;
    blocdropdownValue.value = null;
    appartmentdropdownValue.value = null;
    chambredropdownValue.value = null;
    litaddData.value = {};
    
};
const addlit= async ()=>{
    let data={
        Residenceid:Residenceid,
        blocid:blocdropdownValue.value.id ,
        etageid:EtagedropdownValue.value.id ,
        appartmentid:appartmentdropdownValue.value.id,
        chambreid:chambredropdownValue.value.id ,
        ...litaddData.value
    }
    const response = await litservices.addlit(data);

    toast.add({ severity: response.type, summary: response.summary, detail: response.msg, life: 3000 });
    litaddDialog.value = false;
    refreshComponent();
}
const editlits= (data) =>{
    LitDataEdit.value=data;
    liteditDialog.value=true;
}
const editnbchnb =async ()=>{
    let data ={
        Residenceid:Residenceid,
        blocid:LitDataEdit.value.blocid ,
        etageid:LitDataEdit.value.etageid ,
        appartmentid:LitDataEdit.value.appartmentid,
        indexch:LitDataEdit.value.Indexchambre ,
        indexlit:LitDataEdit.value.Indexlit,
        prixlit:LitDataEdit.value.PrixLit,
        cautionlit:LitDataEdit.value.CotionLit
    }

    const response = await litservices.updateLit(data);

    toast.add({ severity: response.type, summary: response.summary, detail: response.msg, life: 3000 });
    liteditDialog.value = false;
    refreshComponent();

}

const promptdialogdelete =  (data) => {
    deletedata.value=data;
    deletedialog.value=true

};
const daletelit = async () => {
    let data ={
        Residenceid:Residenceid,
        blocid:deletedata.value.blocid ,
        etageid:deletedata.value.etageid ,
        appartmentid:deletedata.value.appartmentid,
        indexch:deletedata.value.Indexchambre ,
        indexlit:deletedata.value.Indexlit,
        prixlit:deletedata.value.PrixLit,
        cautionlit:deletedata.value.CotionLit
    }

    const response = await litservices.deleteLit(data);

    toast.add({ severity: response.type, summary: response.summary, detail: response.msg, life: 3000 });
    deletedialog.value = false;
    refreshComponent();
};
watchEffect(() => {
    if (route.name == "lits") {
        readResidence(route.params.id)
    }
});
onMounted( async () => {
    readResidence(Residenceid)
});

</script>

<template>
    <div class="grid">
        <div class="col-12">
            <div class="card">
                <Toast />
                <Toolbar class="mb-4">
                    <template v-slot:start>
                        <div class="my-2">
                            <Button label="Ajouter" icon="pi pi-plus" class="p-button-success mr-2" @click="openNew" />
                        </div>
                    </template>
                    
                    <template v-slot:end>
                        <Button label="Exporter" icon="pi pi-upload" class="p-button-help" @click="exportCSV($event)" />
                    </template>
                </Toolbar>
                <DataTable
                    ref="dt"
                    :value="LitData"
                    dataKey="name"
                    :paginator="true"
                    :rows="10"
                    paginatorTemplate="FirstPageLink PrevPageLink PageLinks NextPageLink LastPageLink CurrentPageReport RowsPerPageDropdown"
                    :rowsPerPageOptions="[5, 10, 25]"
                    currentPageReportTemplate="Showing {first} to {last} of {totalRecords} Appartments"
                    responsiveLayout="scroll"
                >
                    <Column field="residencename" header="residence" headerStyle="width:10%; min-width:8rem;">
                        <template #body="slotProps">
                            {{ slotProps.data.residencename }} 
                        </template>
                    </Column>
                    <Column field="etagenumber" header="Etage" headerStyle="width:10%; min-width:6rem;">
                        <template #body="slotProps">
                          {{ slotProps.data.etagenumber ==0 ? "RDC" : slotProps.data.etagenumber }} 
                        </template>
                    </Column>
                    <Column field="numappartement" header="Num. Appartement " headerStyle="width:15%; min-width:10rem;">
                        <template #body="slotProps">
                          {{ slotProps.data.numappartement }} 
                        </template>
                    </Column>
                    <Column field="Indexchambre" header="Num Chambre" headerStyle="width:15%; min-width:10rem;">
                        <template #body="slotProps">
                            {{ slotProps.data.Indexchambre }}
                        </template>
                    </Column>
                    <Column field="Indexchambre" header="Num Lit" headerStyle="width:15%; min-width:10rem;">
                        <template #body="slotProps">
                            <a v-if="slotProps.data.LitIsrent" style="color: #ef4444;">{{ slotProps.data.Indexlit }}</a>
                            <a v-else-if=" !slotProps.data.LitIsrent" style="color: #22c55e;">{{ slotProps.data.Indexlit }}</a>

                        </template>
                    </Column>
                    <Column field="PrixLit" header="Prix Lit" headerStyle="width:15%; min-width:10rem;">
                        <template #body="slotProps">
                            <a v-if="slotProps.data.LitIsrent" style="color: #ef4444;">{{ slotProps.data.PrixLit }} DT</a>
                            <a v-else-if=" !slotProps.data.LitIsrent" style="color: #22c55e;">{{ slotProps.data.PrixLit }} DT</a>

                        </template>
                    </Column>                    
                    <Column field="CotionLit" header="Cautionnement" headerStyle="width:15%; min-width:10rem;">
                        <template #body="slotProps">
                            <a v-if="slotProps.data.LitIsrent" style="color: #ef4444;">{{ slotProps.data.CotionLit }} DT</a>
                            <a v-else-if=" !slotProps.data.LitIsrent" style="color: #22c55e;">{{ slotProps.data.CotionLit }} DT</a>

                        </template>
                    </Column>
                                 

                    <Column headerStyle="min-width:10rem;">
                        <template #body="slotProps">
                            <Button icon="pi pi-pencil" class="p-button-rounded p-button-success mr-2" @click="editlits(slotProps.data)" />
                            <Button icon="pi pi-times" class="p-button-rounded p-button-danger mr-2" @click="promptdialogdelete(slotProps.data)" />

                        </template>
                    </Column>
                </DataTable>

                <Dialog v-model:visible="litaddDialog" :style="{ width: '450px' }" header="Ajouter Un lit" :modal="true" class="p-fluid">
                    <div class="field">
                        <label for="name">Choisir Bloc</label>
                        <Dropdown v-model="blocdropdownValue" :options="blocdropdownValues" optionLabel="name" placeholder="Select" @change="getetagesbloc(blocdropdownValue)"/>
                    </div>
                    <div class="field">
                        <label for="name">Choisir Etage</label>
                        <Dropdown v-model="EtagedropdownValue" :options="EtagedropdownValues" optionLabel="name" placeholder="Select" @change="getappartmentsetage(EtagedropdownValue)" />
                    </div>
                    <div class="field">
                        <label for="name">Choisir Appartements</label>
                        <Dropdown v-model="appartmentdropdownValue" :options="appartmentdropdownValues" optionLabel="name" placeholder="Select" @change="getchambappart(appartmentdropdownValue)" />
                    </div>
                    <div class="field">
                        <label for="name">Choisir Chambre</label>
                        <Dropdown v-model="chambredropdownValue" :options="chambredropdownValues" optionLabel="name" placeholder="Select" />
                    </div>
                    <div v-if="chambredropdownValue">
                        <div class="field " >
                            <label>Prix Lit</label>
                            <InputNumber  v-model.trim="litaddData.Prix" />
                        </div>
                        <div class="field">
                            <label>cautionnement Lit</label>
                            <InputNumber  v-model.trim="litaddData.Cotion" />
                        </div>
                    </div>
                    <template #footer>
                        <Button label="Cancel" icon="pi pi-times" class="p-button-text"  @click="litaddDialog=false"/>
                        <Button label="Save" icon="pi pi-check" class="p-button-text"  @click="addlit()" :disabled="!blocdropdownValue||!EtagedropdownValue||!appartmentdropdownValue||!chambredropdownValue"/>
                    </template>
                </Dialog>
                <Dialog v-model:visible="liteditDialog" :style="{ width: '450px' }" header="Modifier Nb. de lit" :modal="true" class="p-fluid">

                    <div class="field">
                        <label for="slider">Chambre Num</label>
                        <InputText   v-model.number="LitDataEdit.Indexchambre" disabled="disabled"/>
                    </div>
                    <div class="field">
                        <label for="slider">Lit Num</label>
                        <InputText   v-model.number="LitDataEdit.Indexlit" disabled="disabled"/>
                    </div>
                    <div class="field">
                        <label for="slider">Prix Lit</label>
                        <InputText   v-model.number="LitDataEdit.PrixLit" />
                    </div>
                    <div class="field">
                        <label for="slider">Prix cautionnement </label>
                        <InputText   v-model.number="LitDataEdit.CotionLit" />
                    </div>

                    
                    <template #footer>
                        <Button label="Cancel" icon="pi pi-times" class="p-button-text"  @click="liteditDialog=false"/>
                        <Button label="Save" icon="pi pi-check" class="p-button-text"  @click="editnbchnb()"/>
                        
                    </template>
                </Dialog>
                <Dialog v-model:visible="deletedialog" :style="{ width: '450px' }" header="Confirm" :modal="true">
                        <div class="flex align-items-center justify-content-center">
                            <i class="pi pi-exclamation-triangle mr-3" style="font-size: 2rem" />
                            <span v-if="deletedialog">Etes vous sur de suprimer  <br> Residence <b>{{deletedata.residencename}}</b> Appartement <b>{{deletedata.numappartement}}</b> Chambre <b>{{deletedata.Indexchambre}}</b> Lit <b>{{deletedata.Indexlit}}</b> ?</span>
                        </div>
                        <template #footer>
                            <Button label="No" icon="pi pi-times" class="p-button-text" @click="deletedialog = false;deletedata=null" />
                            <Button label="Yes" icon="pi pi-check" class="p-button-text" @click="daletelit()" />
                        </template>
                    </Dialog>
            </div>
        </div>
    </div>
</template>

